.intro{
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    transition: background-image 0.5s ease-out;

    ul{
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;

        li{
            list-style: none;

            span{
                position: relative;
                font-size: 4em;
                font-weight: 700;
                color: #fff;
                line-height: 1.25em;
                text-decoration: none;
                text-transform: uppercase;
                transition: 0.5s;
                cursor: default;
                -webkit-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }
        }
    }

    ul:hover li:not(:hover) span{
        opacity: 0.5;
        color: transparent;
        -webkit-text-stroke: 1px #fff;
    }
}