.topbar {
    width: 100%;
    height: 70px;
    background-color: black;
    color: white;
    z-index: 2;
    top: 0;
    transition: all 1s ease;
    border-bottom: 1px solid #999999;
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
                cursor: default;
                -webkit-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }

            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;
            }
    }
}