.portfolioList{
    font-size: 20px;
    background-color: black;
    color: white;
    margin: 20px 40px 20px 0;
    padding: 12px 30px;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    border: solid #eeeeee;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &.active{
        font-size: 20px;
        background-color: white;
        color: black;
    }
}

.portfolioList span {
    display: block;
    position: relative;
}

.portfolioList > span {
    overflow: hidden;
}

.portfolioList > span > span {
    overflow: hidden;
}

.portfolioList:hover > span > span {
    animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
}

@keyframes MoveUpInitial {
    to {
        transform: translate3d(0,-105%,0);
    }
}

@keyframes MoveUpEnd {
    from {
        transform: translate3d(0,100%,0);
    }
    to {
        transform: translate3d(0,0,0);
    }
}

.portfolioList::before {
    content: '';
    background: #000;
    transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
    transform-origin: 100% 50%;
}

.portfolioList:hover::before {
    transform: scale3d(0,1,1);
    transform-origin: 0% 50%;
}