.about{
    background-color:black;
    color:white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    .describe{
        font-size: 40px;
        font-weight: 700;
        text-align: center;
        padding: 10px 300px 0 300px;
    }

    h1{
        font-size: 50px;
    }

    ul{
        margin: 30px 10px;
        padding: 0;
        list-style: none;
        display: flex;

        li{
            font-size: 20px;
            background-color: black;
            color: white;
            margin-right: 40px;
            padding: 12px 30px;
            border-radius: 5px;
            overflow: hidden;
            cursor: pointer;
            border: solid #eeeeee;
            -webkit-user-select: none;
            -ms-user-select: none;
            user-select: none;

            &.active-tabs{
                font-size: 20px;
                background-color: white;
                color: black;
            }
        }
        
        li span {
            display: block;
            position: relative;
        }
        
        li > span {
            overflow: hidden;
        }
        
        li > span > span {
            overflow: hidden;
        }
        
        li:hover > span > span {
            animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
        }
        
        @keyframes MoveUpInitial {
            to {
                transform: translate3d(0,-105%,0);
            }
        }
        
        @keyframes MoveUpEnd {
            from {
                transform: translate3d(0,100%,0);
            }
            to {
                transform: translate3d(0,0,0);
            }
        }
        
        li::before {
            content: '';
            background: #000;
            transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
            transform-origin: 100% 50%;
        }
        
        li:hover::before {
            transform: scale3d(0,1,1);
            transform-origin: 0% 50%;
        }
    }

    .content {
        display: none;
        background-color: #1a1a1a;
        border-radius: 5px;
        overflow: hidden;
    }

    .active-content{
        display: block;
    }

    .container-profil{
        padding: 20px;
        display : flex;
        width: 1300px;

        .right{
            max-width: 55%;
            margin-left: 50px;

            h2 {
                font-size:42px;
                margin-bottom: 20px;
                color: white;
            }

            p{
                color:#eeeeee;
            }

            .flex-box{
                display:flex;
                margin: 20px 0 15px 0;
            }
            .box {
                background-color: #1a1a1a;
                box-shadow: 1px 1px 7px black;
                width: 250px;
                height: 100px;
                border-radius: 10px;
                padding: 40px 40px 0 40px;
                margin: 10px 15px 10px 5px ;

                .title{
                    color:#999999;
                    padding-bottom: 10px;
                }

                p{
                    font-size: 20px;
                    color: white;
                }
            }
        }
    }

    .container-relative{
        border-radius: 10px;
        padding: 10px 20px;
        width: 1300px;

        h2 {
            font-size:42px;
            text-align: center;
            color: white;
        }

        .title{
            padding-top: 10px;
            text-align: center;
            font-size: 20px;
            color:white;
        }
        
        .date{
            text-align: center;
            color: #999999;
        }

        .name{
            padding-top: 10px;
            margin-bottom: 10px;
            text-align: center;
            font-size: 20px;
            color: white;
        }

        hr{
            background-color: #999999;
            height: 1px;
            width: 800px;
            border: none;
            margin: auto;
        }

        ul{
            margin: 10px 0;
        }

        .icon-line {
            display:flex;
            justify-content: center;
        }

        .icon-line .icon{
            display: flex;
            flex-direction: column;
            text-align: center;
        }

        .icon-line img{
            width: 50px;
            height: 50px;
            margin: 5px 20px;
            display: block;
        }

        .icon-line p{
            color: #999999;
        }
    }
}