.app{
    height: 100vh;
    overflow-y: hidden;

    .sections{
        width: 100%;
        height: calc(100vh - 30px);
        position:relative;
        scroll-behavior: smooth;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 30px);
            scroll-snap-align: start;
        }
    }
}